.Wrapper_content {
  padding: 20px 100px;
  display: flex;
  
  @include phone-small {
    padding: 50px 10px;
    display: block;
  }
  @include tablet-down {
    padding: 50px 10px;
    display: block;
  }

  .Wrapper_content_left {
    width: 25%;
   
    @include phone-small {
      width: 100%;
    }
    @include tablet-down {
      width: 100%;
    }
    
    .filter_box {
      display: block;
      .cross {
        display: none;
       
        @include phone-small {
          display: inline-block;
        }
        @include tablet-down {
          display: inline-block;
        }
      }
     }
    .filter_results {
      display: none;

      @include phone-small {
        display: flex;
        justify-content: space-around;
        margin-top: 20px;
        margin-bottom: 20px;
      }

      @include tablet-down {
        display: flex;
        justify-content: space-around;
        margin-top: 20px;
        margin-bottom: 20px;
      }

     }
    
     @include phone-small {
        .filter_box {
        background: white;
        box-shadow: 0 2px 8px rgb(0 0 0 / 25%);
        padding: 10px;
        height: 100vh;
        
        .cross_and_filter {
          display: flex;
          justify-content: space-between ;
        }
    }
    }
 
    @include tablet-down {
      .filter_box {
        background: white;
        box-shadow: 0 2px 8px rgb(0 0 0 / 25%);
        padding: 10px;
        height: 100vh;
        
        .cross_and_filter {
          display: flex;
          justify-content: space-between ;
        }
    }
    }
    

    p {
      font: var(--unnamed-font-style-normal) normal
        var(--unnamed-font-weight-normal) var(--unnamed-font-size-16) /
        var(--unnamed-line-spacing-24) var(--unnamed-font-family-muli);
      letter-spacing: var(--unnamed-character-spacing-0);
      color: #545d63;
      text-align: left;
      font: normal normal normal 16px/24px Muli;
      letter-spacing: 0px;  
    }

    h5 {
      font: var(--unnamed-font-style-normal) normal
        var(--unnamed-font-weight-normal) var(--unnamed-font-size-20) / 14px
        var(--unnamed-font-family-muli);
      text-align: left;
      font: normal normal normal 20px/14px Muli;
      letter-spacing: 0px;
      color: #181c24;
      margin-top: 24px;
    }

    .horizontal_Line {
      width: 224px;
      height: 0px;
      margin-top: 16px;
      border: 2px solid #d1d3d6;
    }
    .Filters {
      display: flex;
      justify-content: flex-start;
      width: 60%;
      margin-top: 22px;
      p {
        padding-left: 10px;
        padding-top: 3px;
      }
    }
  }
  .Wrapper_content_right {
    width: 75%;
    @include phone-small {
      width: 100%;
    }

    @include tablet-down {
      width: 100%;
    }

    .Product_Sorting {
      display: flex;
      justify-content: space-between;
      .cross {
        display: none;
        @include phone-small {
          display: inline-block;
        }

        @include tablet-down {
          display: inline-block;
        }
      }
      @include phone-small {
        display: inline-block;
        .sort_box {
          width: 100%;
          background: white;
          display: flex;
          justify-content: space-around;
          flex-direction: row-reverse;
          box-shadow: 0 2px 8px rgb(0 0 0 / 25%);
          padding: 20px;
      }
      }

      @include tablet-down {
        display: inline-block;
        .sort_box {
          width: 100%;
          background: white;
          display: flex;
          justify-content: space-around;
          flex-direction: row-reverse;
          box-shadow: 0 2px 8px rgb(0 0 0 / 25%);
          padding: 20px;
      }
      }
      h6 {
        font: var(--unnamed-font-style-normal) normal
          var(--unnamed-font-weight-600) var(--unnamed-font-size-16) /
          var(--unnamed-line-spacing-20) var(--unnamed-font-family-muli);
        font: normal normal 600 16px/20px Muli;
        letter-spacing: 0px;
        color: #172026;
      }
      select {
        background: var(--white) 0% 0% no-repeat padding-box;
        border: 2px solid var(--gray-l2);
        background: #ffffff 0% 0% no-repeat padding-box;
        border: 2px solid #b5b7bc;
        border-radius: 8px;
        padding: 7px 20px;

        option {
          font: normal normal normal 16px/24px Muli;
          letter-spacing: 0px;
          color: #172026;
        }
      }
    }

    .Product_Listing {
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      
      .Product_Data {
        width: 30%;
        padding-top: 24px;
        h5 {
          padding-top: 8px;
        }
        p {
          padding-top: 5px;
        }
        i {
          padding-top: 17.5px;
        }
        @include phone-small {
          width: 45%;
        }

        @include tablet-down {
          width: 45%;
        }

        img {
          width: 100%;
          height: 382px;
        }
      }
    }
    .pagination {
      display: flex;
      justify-content: center;
      margin-top: 10px;

      a {
        color: black;
        float: left;
        padding: 8px 16px;
        text-decoration: none;
      }
    }
  }
}
