.content_wrapper {
  display: flex;
  padding: 40px 185px;

  @include phone-small {
    padding: 0px 0px;
    display: block;
  }
  
  @include tablet-down {
    padding: 0px 0px;
    display: block;
  }
  .content_wrapper_left {
    width: 50%;

    @include phone-small {
      width: 100%;
      padding: 25px;
    }

    @include tablet-down {
      width: 100%;
      padding: 25px;
    }

    .product_description_title {
      @include phone-small {
        position: relative;
        top: 20rem;
      }

      @include tablet-down {
        position: relative;
        top: 39rem;
      }
    }

    .image_section {
      display: flex;
      justify-content: space-between;

      .small_images {
        width: 30%;

        @include phone-small {
          display: none;
        }

        @include tablet-down {
          display: none;
        }

        img {
          width: 80%;
          height: 73px;
          padding-top: 10px;
        }
      }
      .bigger_image {
        width: 70%;

        @include phone-small {
          width: 100%;
        }

        @include tablet-down {
          width: 100%;
        }
        img {
          padding-top: 10px;
          width: 100%;
          height: 382px;

          @include phone-small {
            padding: 20px;
            width: 100%;
            height: 300px;
            margin-top: 50px;
          }
          @include tablet-down {
            padding: 20px;
            width: 100%;
            height: 300px;
            margin-top: 50px;
          }
        }
      }
    }

    h5 {
      padding-top: 25px;
      font: var(--unnamed-font-style-normal) normal
        var(--unnamed-font-weight-600) var(--unnamed-font-size-36) /
        var(--unnamed-line-spacing-45)
        var(--unnamed-font-family-source-serif-pro);
      font: normal normal 600 25px/45px Source Serif Pro;
      color: #172026;
    }
    h6 {
      font: var(--unnamed-font-style-normal) normal
        var(--unnamed-font-weight-600) var(--unnamed-font-size-20) /
        var(--unnamed-line-spacing-25) var(--unnamed-font-family-muli);
      font: normal normal 400 18px/25px Muli;
      letter-spacing: 0px;
      color: #172026;
    }
    p {
      font: var(--unnamed-font-style-normal) normal
        var(--unnamed-font-weight-normal) var(--unnamed-font-size-16) /
        var(--unnamed-line-spacing-24) var(--unnamed-font-family-muli);
      font: normal normal normal 14px Muli;
      letter-spacing: 0px;
      color: #172026;
      padding-top: 10px;
      line-height: 1.5;
    }
    .horizontal_line {
      width: 102px;
      height: 8px;
      background: var(--primary-brand) 0% 0% no-repeat padding-box;
      padding-left: 20px;
      margin-top: 15px;
    }
  }
  .content_wrapper_right {
    width: 50%;
    padding: 5px 0px 0px 20px;

    @include phone-small {
      width: 100%;
      padding: 25px;
      position: relative;
      bottom: 25rem;
    }

    @include tablet-down {
      width: 100%;
      padding: 25px;
      position: relative;
      bottom: 22rem;
    }

    @include phone-small {
      .category_heading {
        position: relative;
        bottom: 18rem;
    }
    }

    
    @include tablet-down {
      .category_heading {
        position: relative;
        bottom: 17.5rem;
    }
    }

    

    p {
      font: var(--unnamed-font-style-normal) normal
        var(--unnamed-font-weight-normal) var(--unnamed-font-size-16) /
        var(--unnamed-line-spacing-24) var(--unnamed-font-family-muli);
      letter-spacing: var(--unnamed-character-spacing-0);
      color: #545d63;
      text-align: left;
      font: normal normal normal 16px/24px Muli;
      letter-spacing: 0px;
    }
    h5 {
      font: var(--unnamed-font-style-normal) normal
        var(--unnamed-font-weight-600) var(--unnamed-font-size-36) /
        var(--unnamed-line-spacing-45)
        var(--unnamed-font-family-source-serif-pro);
      font: normal normal 600 25px/45px Source Serif Pro;
      color: #172026;
    }
    .price {
      p {
        font: var(--unnamed-font-style-normal) normal
          var(--unnamed-font-weight-600) var(--unnamed-font-size-20) /
          var(--unnamed-line-spacing-30) var(--unnamed-font-family-muli);
        font: normal normal 600 16px/20px Muli;
        letter-spacing: 0px;
        color: #172026;
      }
    }
    .description {
      p {
        letter-spacing: var(--unnamed-character-spacing-0);
        color: var(--unnamed-color-172026);
        text-align: left;
        letter-spacing: 0px;
        color: #172026;
        font-size: 14px;
        line-height: 1.5;
      }
    }
    hr {
      margin-top: 10px;
      border: 2px solid #d1d3d6;
    }
    .size {
      margin-top: 10px;
      h6 {
        font: var(--unnamed-font-style-normal) normal
          var(--unnamed-font-weight-600) var(--unnamed-font-size-20) /
          var(--unnamed-line-spacing-30) var(--unnamed-font-family-muli);
        font: normal normal 600 20px/30px Muli;
        color: #172026;
      }
      .size_boxes {
        display: flex;
        justify-content: space-between;
        margin-top: 6px;
        .single_size_box {
          width: 50px;
          height: 30px;
          background: var(--white) 0% 0% no-repeat padding-box;
          border: 2px solid var(--gray-m1);
          background: #ffffff 0% 0% no-repeat padding-box;
          border: 2px solid #91959c;
          border-radius: 6px;

          p {
            display: grid;
            place-items: center;
          }
        }
      }

      .Quantity {
        display: flex;
        margin-top: 10px;
        
        @include phone-small {
          justify-content: center;

        }
        @include tablet-down {
          justify-content: center;

        }

        .decrease_quantity,
        .increase_quantity {
          width: 25px;
          height: 25px;
          background: #eaebeb 0% 0% no-repeat padding-box;
          border: 2px solid #b5b8ba;
          border-radius: 50px;
          cursor: pointer;
          text-align: center;
          color: #91959c;
          margin-top: 5px;
          p {
            text-align: center;
            color: #91959c;
            margin-top: -1px;
          }
        }

        .decrease_disabled,
        .increase_disabled {
          width: 25px;
          height: 25px;
          background: darkgray 0% 0% no-repeat padding-box;
          border: 2px solid #b5b8ba;
          border-radius: 50px;
          cursor: pointer;
          text-align: center;
          margin-top: 5px;
        }
        .decrease_disabled,
        .increase_disabled {
          p {
            text-align: center;
            margin-top: -1px;
          }
        }
        #NumberOfProduct {
          width: 64px;
          height: 35px;
          background: var(--white) 0% 0% no-repeat padding-box;
          border: 2px solid var(--gray-m1);
          background: #ffffff 0% 0% no-repeat padding-box;
          border: 2px solid #91959c;
          border-radius: 8px;
          margin-right: 10px;
          margin-left: 10px;
          text-align: center;
          display: grid;
          place-items: center;
        }
      }
    }
    button {
      background: var(--primary-brand) 0% 0% no-repeat padding-box;
      background: #1f39ff 0% 0% no-repeat padding-box;
      border-radius: 28px;
      border: none;
      color: white;
      padding: 12px 105px;
      margin-top: 25px;
      cursor: pointer;
    }
    .save_share {
      width: 70%;
      display: flex;
      justify-content: space-around;
     
      @include phone-small {
        width: 100%;
      }
      @include tablet-down {
        width: 100%;

      }

      .save,
      .Share {
        display: flex;
        padding: 15px 10px;

        p {
          font: var(--unnamed-font-style-normal) normal
            var(--unnamed-font-weight-600) var(--unnamed-font-size-16) /
            var(--unnamed-line-spacing-20) var(--unnamed-font-family-muli);
          color: var(--unnamed-color-172026);
          text-align: left;
          font: normal normal 600 16px/20px Muli;
          letter-spacing: 0.32px;
          color: #172026;
          padding-left: 10px;
        }
      }
    }

    .Details {
      margin-top: 40px;
      h6 {
        font: var(--unnamed-font-style-normal) normal
          var(--unnamed-font-weight-600) var(--unnamed-font-size-20) /
          var(--unnamed-line-spacing-25) var(--unnamed-font-family-muli);
        font: normal normal 600 20px/25px Muli;
        letter-spacing: 0px;
        color: #172026;
      }
      .More_Details {
        display: flex;
        justify-content: space-between;
        margin-top: 8px;

        p {
          font: var(--unnamed-font-style-normal) normal
            var(--unnamed-font-weight-600) var(--unnamed-font-size-16) /
            var(--unnamed-line-spacing-20) var(--unnamed-font-family-muli);
          letter-spacing: var(--unnamed-character-spacing-0);
          color: var(--unnamed-color-172026);
          text-align: left;
          font: normal normal 600 14px/20px Muli;
          letter-spacing: 0px;
          color: #172026;
          margin-top: 5px;
        }
      }
    }
  }
}
