
.Footer {
  .horizontal_line {
    height: 4px;
    background: var(--gray-l1) 0% 0% no-repeat padding-box;
    margin-top: 58px;
  }

  .footer_content_wrapper {
    padding: 44px 100px;
    display: flex;
    justify-content: flex-start;
    max-width: 100%;
   
    @include phone-small {
      padding: 30px 30px;
      display: block;
    }
    
    @include tablet-down {
      padding: 30px 30px;
      display: block;
    }

    .content {
      width: 100%;

      @include phone-small {
        margin-top: 15px;
      }
      
      @include tablet-down {
        margin-top: 15px;
      }

      ul {
        li {
          list-style-type: none;

          h5 {
            font: var(--unnamed-font-style-normal) normal
              var(--unnamed-font-weight-600) var(--unnamed-font-size-16) /
              var(--unnamed-line-spacing-24) var(--unnamed-font-family-muli);
            text-align: left;
            // font: normal normal 600 16px/24px Muli;
            letter-spacing: 0px;
            font-size: 16px;
            font-weight: 600;
            color: #172026;
            padding-bottom: 16px;
          }

          a {
            height: 20px;
            font: var(--unnamed-font-style-normal) normal
              var(--unnamed-font-weight-normal) var(--unnamed-font-size-16) /
              var(--unnamed-line-spacing-24) var(--unnamed-font-family-muli);
            letter-spacing: var(--unnamed-character-spacing-0);
            text-align: left;
            // font: normal normal normal 16px/24px Muli;
            letter-spacing: 0px;
            color: #545d63;
            text-decoration: none;
            padding-top: 10px;
          }
        }
        .Social_icons {
          display: flex;

          img {
            width: 20px;
            height: 20px;
            margin: 2px 10px;
          }
        }
      }
    }
  }
  .Lower_footer_horizontal_line {
    height: 2px;
    background: var(--gray-l1) 0% 0% no-repeat padding-box;
  }

  .wrap_lower_footer_content {
    padding: 44px 100px;
    display: flex;
    justify-content: space-between;

    @include phone-small {
      padding: 30px 10px;
      display: flex;
      flex-direction: column-reverse;
      text-align: center;
    }

    @include tablet-down {
      padding: 30px 10px;
      display: flex;
      flex-direction: column-reverse;
      text-align: center;
    }

    img {
      height: 25px;
      
    @include phone-small {
      display: none;
    }
    @include tablet-down {
      display: none;
    }
    }

    .Copyrights {
      font: var(--unnamed-font-style-normal) normal
        var(--unnamed-font-weight-normal) var(--unnamed-font-size-14) /
        var(--unnamed-line-spacing-17-5) var(--unnamed-font-family-muli);
      font: normal normal normal 14px/18px Muli;
      letter-spacing: 0px;
      color: #545d63;

      @include phone-small {
        margin-top: 15px;
      }
    }

    .Terms_and_Policy {
        a {
            font: var(--unnamed-font-style-normal) normal
            var(--unnamed-font-weight-normal) var(--unnamed-font-size-14) /
            var(--unnamed-line-spacing-17-5) Proxima Nova;
          text-decoration: underline;
          font: normal normal normal 14px/18px Proxima Nova;
          letter-spacing: 0px;
          color: #172026;
          margin: 0px 20px 0px 0px;  
        }
    }
  }
}
