// breaking points

$mobile-width : 320px;

$tablet-width : 768px;

$desktop-width : 1024px;

$desktop-md-width : 1280px;

$desktop-lg-width : 1440px;
$desktop-xl-width : 1920px;



// max-width: 360px and min-width: 320px;

@mixin phone-small {

    @media screen and (min-width: $mobile-width) and (max-width: ($mobile-width + 40)) {
    @content;
 }

}



// max-width: 767px
@mixin tablet-down {
    @media screen and (max-width: ($tablet-width - 1)) {
    @content;
 }

}



// min-width: 768px and max-width: 992px

@mixin tablet {

    @media screen and (min-width: $tablet-width) and (max-width: ($desktop-width - 32)) {

      @content;

    }

}

 

  // max-width: 1279px

  @mixin desktop-md-down{

    @media screen and (max-width: ($desktop-md-width - 1)) {

      @content;

    }

}

   

  // max-width: 991px

  @mixin desktop-down {

    @media screen and (max-width: ($desktop-width - 33)) {

      @content;

    }

}

 

  // min-width: 992px and max-width: 1280px

  @mixin desktop {

    @media screen and (max-width: $desktop-md-width) and (min-width: ($desktop-width - 32)) {

      @content;

    }

}