.Wrapper-content {
  padding: 33px 100px;
  display: flex;

  @include phone-small {
    padding: 0px 0px;
    display: flex;
    flex-direction: column-reverse;
  }
  @include tablet-down {
    padding: 0px 0px;
    display: flex;
    flex-direction: column-reverse;
  }
  .Wrapper-content-left {
    width: 40%;
    height: 272px;
    /* UI Properties */
    background: #1b252c 0% 0% no-repeat padding-box;
    opacity: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    @include phone-small {
      width: 90%;
      height: 159px;
      margin: -40px auto;
    }

    @include tablet-down {
      width: 90%;
      height: 159px;
      margin: -40px auto;
    }

    h3 {
      height: 121px;
      letter-spacing: var(--unnamed-character-spacing-0);
      color: var(--white);
      text-align: center;
      font: normal normal 600 40px/50px Source Serif Pro;
      letter-spacing: 0px;
      color: #ffffff;
    }
    .horizontal_line {
      width: 102px;
      height: 8px;
      background: var(--primary-brand) 0% 0% no-repeat padding-box;
      padding-left: 20px;
    }
  }

  .Wrapper-content-right {
    width: 60%;

    @include phone-small {
      width: 100%;
    }
    @include tablet-down {
      width: 100%;
    }
    img {
      height: 252px;
      padding: 20px 0px 0px 0px;
      width: 100%;

      @include phone-small {
        height: 193px;
        width: 100%;
        padding: 0px;
      }
      @include tablet-down {
        height: 193px;
        width: 100%;
        padding: 0px;
      }
    }
  }
}
