* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.navbar {
  height: 70px;
  width: 100%;
  padding: 20px 100px;
  background-color: #fff;
  display: flex;
  justify-content: space-between;
  position: relative;

  .nav-header {
    // display: inline;

    .nav-logo {
      // display: inline-block;
      // margin-top: -7px;
      img {
        height: 25px;
      }
    }
  }

  .nav-links {
    display: flex;
    font-size: 16px;

    a {
      padding: 0px 12px;
      text-decoration: none;
      font-weight: 600;
      color: #1b252c;
      // height: 20px;
      font-size: 16px;

      letter-spacing: 0px;
      &:hover {
        background-color: rgba(0, 0, 0, 0.3);
      }
    }
  }

  #nav-check,
  .nav-btn {
    display: none;
  }
  .Right_Nav_section {
    img {
      width: 22px;
      height: 22px;
    }

    .icon-wrapper {
      position: relative;
      float: left;
    }

    *.icon-blue {
      color: Chartreuse;
    }

    *.icon-grey {
      color: yellow;
    }

    i {
      text-align: center;
      vertical-align: middle;
    }

    .badge {
      background: #1b252c 0% 0% no-repeat padding-box;
      width: 30px;
      height: 30px;
      margin: 0;
      border-radius: 50%;
      position: absolute;
      top: -14px;
      right: -20px;
      padding: 6px;
      text-align: center;
      cursor: pointer;
      color: white;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: 600;
    }
  }
}

/* Hover effects */

/* responsive navbar toggle button */

@media (max-width: 800px) {
  .navbar {
    padding: 20px 10px;
    /* height: 70px; */
    width: 100%;
    background-color: #fff;
    /* display: flex; */
    display: block;
    position: relative;

    .nav-btn {
      display: inline-block;
      position: absolute;
      top: 0px;
      right: 0px;

      label {
        display: inline-block;
        width: 80px;
        height: 70px;
        padding: 25px;

        span {
          display: block;
          height: 10px;
          width: 25px;
          border-top: 3px solid #1b252c;
        }

        // &:hover {
        //   background-color: #1B252C;
        //   border-top: 3px solid #fff;

        // }
      }
    }

    // #nav-check:checked ~ .nav-btn label {
    //   background-color: rgb(9, 14, 90);
    // }

    .nav-links {
      // position: absolute;
      position: relative;
      display: block;
      text-align: center;
      width: 100%;
      background-color: white;
      transition: all 0.3s ease-in;
      overflow-y: hidden;
      top: 142px;
      right: 10px;
      margin-top: -120px;
      line-height: 1.8;
      a {
        display: block;
        line-height: 1.8;
      }
    }

    #nav-check {
      &:not(:checked) ~ .nav-links {
        height: 0px;
      }

      &:checked ~ .nav-links {
        height: calc(100vh - 250px);
        overflow-y: auto;
        width: 105.5%;
      }
    }

    .nav-links .loginBtn {
      padding: 10px 40px;
      margin: 20px;
      font-size: 18px;
      font-weight: bold;
      color: rgb(9, 14, 90);
    }
    .Right_Nav_section {
      .icon-wrapper {
        position: relative;
        float: initial;
        margin-top: 12px;
        .badge {
          width: 27px;
          height: 27px;
          position: absolute;
          top: -10px;
          right: 155px;
        }
      }
    }
  }

