.wrapper_content {
  padding: 45px 100px;

  @include phone-small {
    padding: 10px 10px;
  }

  @include tablet-down {
    padding: 10px 10px;
  }

  h4 {
    font: var(--unnamed-font-style-normal) normal var(--unnamed-font-weight-600)
      var(--unnamed-font-size-48) / var(--unnamed-line-spacing-45)
      var(--unnamed-font-family-source-serif-pro);
    text-align: center;
    font: normal normal 600 42px/40px Source Serif Pro;
    letter-spacing: 0px;
    color: #172026;

    @include phone-small {
      font: normal normal 600 25px/40px Source Serif Pro;
      margin-top: 40px;
    }

    @include tablet-down {
      font: normal normal 600 25px/40px Source Serif Pro;
      margin-top: 40px;
    }
  }
  .horizontal_line {
    width: 102px;
    height: 8px;
    background: var(--primary-brand) 0% 0% no-repeat padding-box;
    margin: 20px auto;
  }
  .product_content {
    display: flex;
    justify-content: space-between;

    @include phone-small {
      display: block;
    }
    
    @include tablet-down {
      display: block;
    }

    .Product_listing {
      width: 70%;
      @include phone-small {
        width: 100%;
        display: block;
      }

      @include tablet-down {
        width: 100%;
        display: block;
      }
      .product_Listing_in_cart {
        width: 100%;
        display: flex;
        margin-top: 20px;
        @include phone-small {
          width: 100%;
          display: block;
        }

        @include tablet-down {
          width: 100%;
          display: block;
        }

        .Details_Product {
          display: flex;
          width: 40%;
          @include phone-small {
            width: 100%;
            display: flex;
            justify-content: space-around;
          }

          @include tablet-down {
            width: 100%;
            display: flex;
            justify-content: space-around;
          }
          img {
            height: 125px;
            width: 100px;
            margin-right: 10px;
          }
          .three_dots {
            display: none;
            @include phone-small {
              display: flex;
            }

            @include tablet-down {
              display: flex;
            }
            .dot {
              width: 2px;
              height: 2px;
              background: #1d1d1b 0% 0% no-repeat padding-box;
              margin-left: 3px;
              border-radius: 50px;

            }
          }
        }

        .Increase_Product {
          display: flex;
          justify-content: space-around;
          width: 60%;

          @include phone-small {
            width: 100%;
          }

          
          @include tablet-down {
            width: 100%;          
          }

          .Quantity {
            display: flex;

            .decrease_quantity,
            .increase_quantity {
              width: 25px;
              height: 25px;
              background: #eaebeb 0% 0% no-repeat padding-box;
              border: 2px solid #b5b8ba;
              border-radius: 50px;
              cursor: pointer;
              text-align: center;
              color: #91959c;
              margin-top: 5px;

              p {
                text-align: center;
                color: #91959c;
                margin-top: -1px;
              }

            }
            #NumberOfProduct {
              width: 64px;
              height: 35px;
              background: var(--white) 0% 0% no-repeat padding-box;
              border: 2px solid var(--gray-m1);
              background: #ffffff 0% 0% no-repeat padding-box;
              border: 2px solid #91959c;
              border-radius: 8px;
              margin-right: 10px;
              margin-left: 10px;
              text-align: center;
              display: grid;
              place-items: center;
            }
          }

          .extra_details {
            button {
              display: none;
            }
          }
          @include phone-small {
            .extra_details {
        
              margin-top: -128px;
              margin-left: 70px;
              background: white;  

              button {
                margin-left: 80%;
                border: none;
                background: white;
                font-size: 15px;
              }
            }
          }

          .Edit_Remove_Product {
            display: flex;
            margin-top: 10px;

            @include phone-small {
              font-size: 13px;
            }

            @include tablet-down {
              font-size: 15px;         
            }
            img {
              height: 18px;
              margin-right: 10px;

              @include phone-small {
                height: 14px;
                margin-right: 10px;
              }

              @include tablet-down {
                height: 16px;
                margin-right: 10px;              
              }
            }
          }
        }
      }
    }

    .Pricing_summary {
      width: 30%;
      @include phone-small {
        width: 100%;
        margin-top: 30px;
      }
      @include tablet-down {
        width: 100%;
        margin-top: 30px;             
      }
      
      .Card {
        padding: 1rem;
        box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
        border-radius: 14px;
        background-color: white;

        h5 {
          font: var(--unnamed-font-style-normal) normal
            var(--unnamed-font-weight-600) var(--unnamed-font-size-20) /
            var(--unnamed-line-spacing-25) var(--unnamed-font-family-muli);
          letter-spacing: var(--unnamed-character-spacing-0);
          color: var(--unnamed-color-172026);
          text-align: left;
          font: normal normal 600 18px/25px Muli;
          letter-spacing: 0px;
          color: #172026;
        }
        .sub_total {
          display: flex;
          justify-content: space-between;
          margin-top: 24px;
          p {
            font: var(--unnamed-font-style-normal) normal
              var(--unnamed-font-weight-normal) var(--unnamed-font-size-16) /
              var(--unnamed-line-spacing-24) var(--unnamed-font-family-muli);
            letter-spacing: var(--unnamed-character-spacing-0);
            color: var(--unnamed-color-172026);
            text-align: left;
            font: normal normal normal 16px/24px Muli;
            letter-spacing: 0px;
            color: #172026;
          }
        }
        button {
          padding: 10px 50px;
          background: var(--primary-brand) 0% 0% no-repeat padding-box;
          background: #1f39ff 0% 0% no-repeat padding-box;
          border-radius: 28px;
          cursor: pointer;
          border: none;
          color: white;
          margin-left: 75px;
          margin-top: 25px;
        }
        .paypalbtn {
          background-color: darkorange;
        }
      }
    }
  }
}
